import * as FilePond from 'filepond';

const inputElement = document.querySelector('input[type="file"]');
const pond = FilePond.create( inputElement ); 


pond.setOptions({
    labelIdle: '   <span class="filepond--label-action">  </span>',
    labelFileLoading: '',
    labelFileLoadError: '    '
});


