function tabs() {
    $('[data-tab]').each(function () { //         

        $(this).click(function () { //  ""  

            let target = $(this).attr('data-tab'); 

            $(this).hide(); 

            $("#" + target).fadeIn();

        })
    })

    $('[data-tab-close="close"]').on("click", function(){
        $(this).parent(".form__hide-input").fadeOut();
        let icon = $(this).parent(".form__hide-input").attr("id");
        $('[data-tab="'+icon+'"]').show();
    })
}


tabs();